import React, { useState } from 'react';
import PrivacyPolicy from './PrivacyPolicy';

export default function Footer() {
  const [ privacyPolicyVisible, setPrivacyPolicyVisible ] = useState(false);

  const togglePrivacyPolicy = e => {
    e.preventDefault();
    setPrivacyPolicyVisible(!privacyPolicyVisible);
  }

  return (
    <footer>
      <div className="container footer__content">
        <p>
          &copy; <a href="https://markrabey.com">Mark Rabey</a> 2020. All Rights Reserved.
        </p>
        <ul className="list-inline">
          <li className="list-inline-item">
            <button className="btn btn-link" style={{ fontSize: 12 }} onClick={ togglePrivacyPolicy }>Privacy Policy</button>
          </li>
        </ul>
      </div>
      <PrivacyPolicy
        visible={ privacyPolicyVisible }
        dismiss={ togglePrivacyPolicy }
      />
    </footer>
  );
}
